<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <div style="display: flex; align-items: center">卡片名称</div>
        <el-button size="small" type="primary" @click="addClick">添加分类</el-button
        >
      </div>
      <el-table :data="articleClass" border style="width: 100%">
        <el-table-column
          label="序号"
          type="index"
          width="100"
        ></el-table-column>
        <el-table-column label="分类" prop="ChannelName"> </el-table-column>
        <el-table-column label="分类别名" prop="Html"> </el-table-column>
        <el-table-column label="操作" width="144">
          <template slot-scope="scope">
            <el-button @click="updateFn(scope.row)" type="primary" size="small">修改</el-button>
            <el-button @click="removeFn(scope.row)" type="danger" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加弹窗 -->
    <el-dialog title="添加文章分类" :visible.sync="isShow" width="30%">
      <el-form :model="form" :rules="rules" ref="form" label-width="80px">
        <el-form-item label="活动名称" prop="ChannelName">
          <el-input v-model="form.ChannelName"></el-input>
        </el-form-item>
        <el-form-item label="分类别名" prop="Html">
          <el-input v-model="form.Html"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false">取 消</el-button>
        <el-button type="primary" @click="add">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isShow: false,
      isEdit: false ,// 保存编辑状态(false新增, true编辑)
      form: {
        ChannelName: "",
        Html: "",
      },
      rules: {
        ChannelName: [
          { required: true, message: "请输入活动名称", trigger: "change" },
          { min: 3, message: "长度不能低于3个字符", trigger: "blur" },
        ],
        Html: [
          { required: true, message: "请输入分类别名", trigger: "blur" },
          { min: 3, message: "长度不能低于3个字符", trigger: "blur" },
        ],
      },
      articleClass: [
        {
          ChannelName: "文章一",
          Html: "wenzhang1",
        }
      ],
    };
  },
  created(){
    // this.getChannel()
  },
  methods: {
    getChannel(){
      this.$axios.get("/index.php/Api/GetChannel",{
          params: {
              Field: "ChannelID",
              ChannelID: '17',
              ShowHidden: 1,
          },
        }).then((res) => {
          this.articleClass = res.data.Data;
          console.log(res)
        })
        .catch((error) => {
          console.log(error);
        });
    },
    addClick(){
      this.isEdit=false 
      this.isShow=true
      this.form={ChannelName: "",Html: ""}
    },
    add() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if(!this.isEdit){
            this.isShow=false
            this.articleClass.push({ ...this.form });
          }else{
           this.articleClass=this.articleClass.map(t=>{
              return t.ChannelName==this.form.ChannelName?{...this.form}:t
            })
            this.isShow=false
          }
        } else {
          console.log("error");
          return false;
        }
      });
    },
    updateFn(row) {
      this.isEdit = true // 设置编辑状态
      this.isShow = true // 让对话框显示
      // 设置数据回显
      this.$nextTick(() => {
        // 先让对话框出现, 它绑定空值的对象, 才能resetFields清空用初始空值
        this.form.ChannelName = row.ChannelName
        this.form.Html = row.Html
      })
    },
    removeFn(row){
      this.articleClass.map((p,i)=>{
        if(p.ChannelName.indexOf(row.ChannelName)>-1){
          this.articleClass.splice(i,1)
        }
      })
    }
  },
};
</script>
<style lang="scss" scoped>
.clearfix {
  display: flex;
  justify-content: space-between;
  align-content: center;
}
.el-dialog__body {
  padding-bottom: 0;
}
</style>